
@media (max-width: 375px){
    main,body,html{
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
    }
    header{
        width: 100%;
        display: block;
    }
    nav ul{
        width: 100%;
        background-color: #3882F6;
        height: 100%;
        display: flex;
        flex-direction: column;
        position: fixed;
        right: -100%;
        top: 80px;
        align-items: flex-start;
        padding-top: 20px;
        gap: 20px;
        transition: all .5s;
        padding-left: 20px;
        padding-bottom: 20px;
        border-radius: 0 0 0 10px;
    }
    .Link{
        color: whitesmoke;
        font-size: 12px;
    }
    .bars{
        display: flex;
        margin-top: 25px;
    }
    .header{
        margin-top: 20px;
    }
    .logo{
        justify-content: flex-start;
        gap: 150px;
    }
/*the code below this line of comment if for home page*/
.home{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 20px;
    padding: 0 20px 50px 20px;
}
img{
    width: 150px;
    height: 200px;
    transform: translateY(0%);
    display: block;
}
.desc,.title{
    padding: 5px 20px;
    width: 100%;
    text-align: center;
}
.btn{
  font-size: 14px;
  padding: 5px 10px;
}
 .top{
    flex: 1;
 }   
}

