 .con{
   padding-top: 80px;
 }
 .container{
    display: flex;
    justify-content: center;
    gap: 20px;
    padding-bottom: 20px;
 }
 
 .Form{
    background-color: grey;
    padding: 40px 40px;
    border-radius: 12px;
 }
 .Form input{
    margin: 2px 0 10px 0;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    outline: 0;
    border: none;
 }
 textarea{
    margin: 5px 0 15px 0;
    border-radius: 7px;
    border: none;
    padding: 5px;
    outline: 0;
 }
.BTN{
    width: 50%;
    background-color: #3882F6;
    padding: 7px 30px;
    border: none;
    border-radius: 5px;
    color: #F9FAF8;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
}
.contact{
    text-align: center;
    padding: 20px 0;
}
label{
    font-size: 19px;
    color: whitesmoke;
}

/*the codes below this lie all all for media query which make my web seen to mobile user responsivery*/
@media (max-width: 375px){
   .Form{
      background-color: grey;
      padding: 20px 5px;
      border-radius: 12px;
      flex-shrink: 1;
      text-align: center;
      width: 90%;
   }
   .Form input{
      padding: 0;
      margin: 2px 0 6px 0;
      width: 80%;
      height: 20px;
   }
   .container{
      width: 100%;
      margin: 20px 20px 20px 0;
      flex-wrap: wrap;
      flex-direction: column;
      align-items: center;
   }
   
 textarea{
   width: 80%;
   height: 30px;
}
.BTN{
   width: 30%;
   padding: 5px 15px;
   font-size: 13px;
 
}
}