
body,.main{
   
    background-color: rgb(203, 206, 209);
    scroll-behavior: smooth;
    height: 100%;
}

.one{
    display: flex;
    gap: 50px;
    justify-content: center;
    padding: 50px 100px;
    margin-top: 50px;
   
}
.profile{
    text-align: center;
    background-color: whitesmoke;
    height: 250px;
    padding: 10px;
    border-radius: 10px;
    padding-left: 30px;
}
.pic{
    width: 80%;
}
.pro{
   
    height: 130px;
    border-radius: 50%;
    height: 130px;
    margin-top: 10px;
    transform: translateX(15%);
}

.skills{
    width: 90%;
    background-color: whitesmoke;
    height: 250px;
    border-radius: 10px;
    padding: 10px 20px;
   
}
.skills div{
    padding: 5px 0;
}
.skills div span,p span{
    filter: brightness(.3);
    opacity: .4;
    font-weight: bold;
}
.skills h3{
    padding: 5px 0;
}    
.two{
    padding: 0 50px 20px 50px;
    width: 100%;
}
.two div{
    background-color: whitesmoke;
    border-radius: 10px;
    padding: 30px 20px;
}
.two div p{
    padding: 5px 0;
}
/*the styles below this line all for mobile device responsiveness*/
@media (max-width: 375px){
    
.one{
    display: block;
    gap: 20px;
    align-items: center;
    flex-direction: column;
    width: 100%;
   padding: 30px 20px 10px 20px;
   scroll-behavior: smooth;
}
    .pro{
        width: 120px;
        height: 120px;
        border-radius: 50%;
        transform: translateX(20%);
        margin-top: 10px;
        scroll-behavior: inherit;
    }
    .profile{
        text-align: center;
        padding: 10px;
        width: 100%;
        flex: 1;
    }
    .skills{
        padding: 10px;
        width: 100%;
        height: auto;
        flex: 1;
    }
           
.two{
    padding-bottom: 20px;
}

.two div p{
    padding: 2px 0;
}
}

/*this is for tablet and other divices*/
@media (max-width: 1167px){
    
.one{
    display: flex;
    gap: 40px;
    justify-content: center;
    padding: 50px 50px;
    flex-wrap: wrap;
   
}

.skills{
    width: 100%;
    height: auto;
    border-radius: 10px;
    padding: 10px 20px;
   
}

.profile{
    height: auto;
}
}