*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
}
:root{
    --header: 5rem;
    --footer: 50px;
    --main: calc(100vh-calc(var(--header)+var(--footer)));
    --backColor: #1F2937;
    --NavLinkColor: #E5E7EB;
}
html{
    height: 100%;
}
.App{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
header{
    height: var(--header);
    background-color: var(--backColor);
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
}
nav ul{
    display: flex;
    gap: 40px;
    padding: 0 40px 0 20px;
}
nav ul li{
    flex-shrink: 1;
}
.Link{
    text-transform: uppercase;
    color: var(--NavLinkColor);
    font-size: 14px;
}
.logo{
    margin-right: auto;
    display: flex;
    flex: 1;
    justify-content: space-between;
}
header h1{
    padding-left: 30px;
    color: whitesmoke;
   
}

.bars{
    display: none;
    flex-direction: column;
    width: 40px;
    height: auto;
    margin-right: 20px;
    gap: 5px;
    cursor: pointer;
}
.bars div{
    background-color: whitesmoke;
    width: 100%;
    height: 5px;
   
}
footer{
    height: var(--footer);
    width: 100%;
    background-color: var(--backColor);
    margin-top: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
/*the code below are all for home page*/
.home{
    display: flex;
    flex-wrap: wrap;
    height: auto;
    padding-left: 100px;
    gap: 40px;
    padding-top: 150px;
    margin-bottom: auto;
}
img{
    border-radius: 10px;
}
.btn{
    background-color: #3882F6;
    padding: 8px 30px;
    border: none;
    border-radius: 5px;
    color: #F9FAF8;
    font-weight: bold;
    font-size: 18px;
    margin-top: 15px;
    cursor: pointer;
}
.pitch{
    font-size: 20px;
}
